import React from "react";
import { Card, Statistic } from "antd";
import { DashboardOutlined } from "@ant-design/icons/es/icons";

export default function TotalDashboards(props: {
  total: number | undefined;
  running: number | undefined;
}) {
  return (
    <Card bordered={false}>
      <Statistic
        title="Total running dashboards"
        value={props.running}
        valueStyle={{ color: "#1890ff" }}
        suffix={`/ ${props.total} Dashboards`}
        prefix={<DashboardOutlined />}
      />
    </Card>
  );
}
